import * as React from "react";
import type { HeadFC } from "gatsby";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import { graphql } from "gatsby";
import Item from "../page/Item";
import Form from "react-bootstrap/Form";
import Parse, { runCloudFunction } from "../vendor/ParseApp";
import Table from "react-bootstrap/Table";
import { timeSince } from "../helpers/time";
import { handleLocation } from "../helpers/data";
import AppContext from "../context/AppContext";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";

const handleData = (key, e) => {
  if (Array.isArray(e)) {
    return e.map((obj) => <Badge> {obj.label} </Badge>);
  }

  if (key === "docUrl") {
    return (
      <a className="color-brand-1" href={e}>
        Download
      </a>
    );
  }

  if (Object.prototype.toString.call(e) === "[object Date]") {
    return timeSince(e);
  }

  if (typeof e === "string") {
    return <Badge> {e}</Badge>;
  }
};

const Header = ({ data }) => {
  const { t } = useTranslation();
  const dataModel = data.get("data");
  const jobTag =
    dataModel?.tags && dataModel?.tags.map((tag) => tag.name).join(" ");

  return (
    <div className="d-flex flex-row">
      <div>{`Looking For job Matching ${jobTag}`}</div>
      <div className="ms-1">
        {handleData("", data.get("createdAt"))} ago
      </div>
    </div>
  );
};

const Body = ({ data }) => {
  const { t } = useTranslation();

  const [profile, setProfile] = React.useState(null);
  const dataModel = data.get("data");
  const jobTag =
    dataModel?.tags && dataModel?.tags.map((tag) => tag.name).join(" ");
  return (
    <div className="d-flex flex-column">
      <h3>Application Detail</h3>

      <Row>
        {Object.keys(dataModel)
          .filter((e) =>
            [
              "tag",
              "expectSalary",
              "noticePeriod",
              "location",
              "docUrl",
              "citizenship"
            ].includes(e)
          )
          .map((key) => (
            <Col>
              <div>{t(key)}:</div> <div>{handleData(key, dataModel[key])}</div>
            </Col>
          ))}
      </Row>
      <h3>Contact Details</h3>
      <Row>
        <Col>


          {
            !profile && <Button onClick={() => {

            runCloudFunction("getProfileByID", { id: data.get('owner') }).then((item) => {
              setProfile(item)
            });
          }}>Request</Button>
          }
          <Row>
          {
            profile && Object.keys(profile.get('data'))
            .filter(e => ['telCode', 'contact'].includes(e))
            .map((key) => (
              <Col>
                <div>{t(key)}:</div> <div>{handleData(key, profile.get('data')[key])}</div>
              </Col>
            ))
          }
          </Row>
        </Col>
      </Row>
    </div>
  );
};

const IndexPage = () => {
  const [items, setItems] = React.useState([]);
  const { t } = useTranslation();

  const context = React.useContext(AppContext);
  const i18context = React.useContext(I18nextContext);
  React.useEffect(() => {
    context.showLoading(true);
    runCloudFunction("searchApplication").then((items) => {
      setItems(items);
      context.showLoading(false);
    });
  }, []);
  const handleData = (data) => {

  }
  return (
    <Container className="">
      {t("location")}
      <h3>Search Application</h3>
      <Row>
        <Col xs="6">
          <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
            <Form.Control type="text" placeholder="keyword search" />
          </Form.Group>
        </Col>
        <Col>
          <i class="bi bi-filter-square"></i>
        </Col>
      </Row>

      <Row>
        <Col className="" onClick={() => {}}>
          { items.length } result
          <Item 
            onClick={handleData}
            items={items} Body={Body} Header={Header} />
        </Col>
      </Row>
    </Container>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
